      </div>
      <div class="videoBlock" v-for="item in videoJson[1].video" :key="item">
        <div
          class="ratio ratio-16x9 videoStyle"
          v-if="item.id === 'wk_mz3r2ZBs'"
        >
          <iframe
            :src="item.video"
            class="embed-responsive-item"
            loading="lazy"
            allow="autoplay;"
            allowfullscreen
          ></iframe>
          <div class="overlay" @click="previewImgClick(item, $event)">
            <CIcon :icon="cilMediaPlay" class="setIcon" />
