<script>
import { reactive, watch, ref } from "vue";
import { useI18n } from "vue-i18n";
import json from "@/assets/press/videoList.json";
import { CIcon } from "@coreui/icons-vue";
import { cilMediaPlay } from "@coreui/icons";
import axios from "axios";
export default {
  setup() {
    const videoJson = reactive(json);

    // 切換不同語系圖片
    const { locale } = useI18n();

    watch(locale, () => {
      changeImg();
    });
    const tpw1Img1 = ref("");

    const changeImg = () => {
      if (locale.value == "zh") {
        tpw1Img1.value = require("@/assets/products/robotics/tpw1Img1_zh.webp");
        console.log(tpw1Img1.value);
      } else {
        tpw1Img1.value = require("@/assets/products/robotics/tpw1Img1_en.webp");
        console.log(tpw1Img1.value);
      }
    };

    changeImg();

    const tpw1Table2 = [
      {
        img: require("@/assets/products/robotics/tpw1Img3.webp"),
      },
    ];

    const loadThumbnail = () => {
      let videos = videoJson[1].video;
      for (let i = 1; i < 5; i++) {
        let url = `https://www.googleapis.com/youtube/v3/videos?id=${videos[i].id}&key=AIzaSyBXCxbE2nHTvZ7oQgVfiVT48HnDLR19sKI&part=snippet`;
        console.log("request url: " + url);
        axios(url)
          .then(function (response) {
            // handle success
            console.log(response);

            if (response.data.items[0].snippet.thumbnails.maxres != null)
              videos[i].thumbnail =
                response.data.items[0].snippet.thumbnails.maxres.url;
            else
              videos[i].thumbnail =
                response.data.items[0].snippet.thumbnails.high.url;
          })
          .catch(function (error) {
            // handle error
            console.log(
              `youtube api request failed! id = ${videos[4].id}`,
              error
            );
          });
      }
    };

    loadThumbnail();
    const previewImgClick = (item, e) => {
      var clickedElement = e.target;
      $(clickedElement).hide();
      // show video
      console.log("clickedElement", clickedElement);
      item.video = `https://www.youtube.com/embed/${item.id}?autoplay=1`;
    };
    return {
      tpw1Table2,
      videoJson,
      previewImgClick,
      CIcon,
      cilMediaPlay,
      tpw1Img1,
    };
  },
};
</script>
